<script>
	import { onMount } from "svelte";
	import { bbox } from "@turf/turf";
	import Panel from "./Panel.svelte";
	import Group from "./Group.svelte";
	import Map from "./Map.svelte";
	import MapSource from "./MapSource.svelte";
	import MapLayer from "./MapLayer.svelte";
	import ColChart from "./charts/ColChart.svelte";
	import Loader from "./ui/Loader.svelte";
	import Select from "./ui/Select.svelte";
	import { getData, getNomis, getBreaks, getTopo, processData } from "./utils.js";

	// CONSTANTS
	const apiurl = "https://www.nomisweb.co.uk/api/v01/dataset/";
	const apikey = "0x3cfb19ead752b37bb90da0eb3a0fe78baa9fa055";
	const geography = "TYPE298";
	const mapstyle =
		"https://bothness.github.io/ons-basemaps/data/style-omt.json";
	const tabledata = "./data/indicators.json";
	const ladtopo = {
		url: "./data/lad_boundaries_2020.json",
		layer: "LA2020EW",
		code: "AREACD",
		name: "AREANM",
	};
	const lsoabldg = {
		url: "https://cdn.ons.gov.uk/maptiles/t18/tiles/{z}/{x}/{y}.pbf",
		layer: "imddata2",
		code: "lsoa11cd",
	};
	const lsoabounds = {
		url:
			"https://cdn.ons.gov.uk/maptiles/administrative/lsoa/v1/boundaries/{z}/{x}/{y}.pbf",
		layer: "boundaries",
		code: "AREACD",
	};
	const lsoadata = "./data/lsoa2011_lad2020.csv";
	const colors = {
		base: ["#d5f690", "#5bc4b1", "#2e9daa", "#0079a2", "#005583", "#cccccc"],
		muted: ["#f5fce2", "#d7ede8", "#cbe2e5", "#c2d7e3", "#bdccd9", "#f0f0f0"],
	};

	// OBJECTS
	let map = null;

	// DATA
	let indicators;
	let ladbounds;
	let ladlookup;
	let ladlist;
	let lsoalookup;
	let data = {};
	let active = {
		lsoa: {
			selected: null,
			hovered: null,
		},
		lad: {
			selected: null,
			selectedPrev: null,
			hovered: null,
			highlighted: null,
		},
	};

	// STATE
	let selectItem;
	let selectMeta;
	let selectData;
	let selectLSOA;
	let selectLAD;
	let loading = true;

	let mapZoom = null;

	function initialise() {
		getTopo(ladtopo.url, ladtopo.layer)
			.then((geo) => {
				ladbounds = geo;

				let lookup = {};
				let list = [];
				geo.features.forEach((f) => {
					lookup[f.properties[ladtopo.code]] = {
						code: f.properties[ladtopo.code],
						name: f.properties[ladtopo.name]
					};
					list.push(lookup[f.properties[ladtopo.code]]);
				});

				list.sort((a, b) => a.name.localeCompare(b.name));
				ladlist = list;

				return lookup;
			})
			.then((lookup) => {
				ladlookup = lookup;

				getData(lsoadata)
					.then((data) => {
						let lookup = {};
						data.forEach((d) => {
							lookup[d.code] = {
								name: d.name,
								parent: d.parent,
							};

							if (!ladlookup[d.parent].children) {
								ladlookup[d.parent].children = [d.code];
							} else {
								ladlookup[d.parent].children.push(d.code);
							}
						});
						return lookup;
					})
					.then((lookup) => {
						lsoalookup = lookup;

						fetch(tabledata)
							.then((res) => res.json())
							.then((json) => {
								indicators = json;

								selectItem = indicators[0].children[4].children[1];
							});
					});
			});
	}

	function setSelect() {
		if (!(selectMeta && selectItem && selectMeta.code == selectItem.code)) {
			let code = selectItem.code;
			let group = indicators.find((d) => d.code == code.slice(0, 3));
			let table = group.children.find((d) => d.code == code.slice(0, 7));
			let cell = +code.slice(7, 10);

			selectMeta = {
				code: selectItem.code,
				group: group,
				table: table,
				cell: cell,
			};

			loadData();
		}
	}

	function loadData() {
		console.log("loading data...");
		loading = true;
		if (data[selectItem.code]) {
			selectData = data[selectItem.code];
			console.log("data loaded from memory!");
			if (active.lad.selected) {
				setColors();
			}
			loading = false;
		} else {
			let url = `${apiurl}${selectMeta.table.nomis}${selectMeta.cell}&geography=${geography}&uid=${apikey}`;
			getNomis(url, selectMeta.cell).then((res) => {
				let dataset = {
					lsoa: {},
					lad: {},
					ew: {},
				};
				res.sort((a, b) => a.perc - b.perc);
				dataset.lsoa.data = res;
				let breaks = getBreaks(res.map((d) => d.perc));
				dataset.lsoa.breaks = breaks;

				dataset.lsoa.data.forEach((d) => {
					if (d.perc <= breaks[2]) {
						d.color = colors.base[0];
						d.muted = colors.muted[0];
						d.fill = colors.base[0];
					} else if (d.perc <= breaks[4]) {
						d.color = colors.base[1];
						d.muted = colors.muted[1];
						d.fill = colors.base[1];
					} else if (d.perc <= breaks[6]) {
						d.color = colors.base[2];
						d.muted = colors.muted[2];
						d.fill = colors.base[2];
					} else if (d.perc <= breaks[8]) {
						d.color = colors.base[3];
						d.muted = colors.muted[3];
						d.fill = colors.base[3];
					} else {
						d.color = colors.base[4];
						d.muted = colors.muted[4];
						d.fill = colors.base[4];
					}
				});

				let proc = processData(res, lsoalookup);
				dataset.lsoa.index = proc.lsoa.index;

				dataset.lad.data = proc.lad.data;
				dataset.lad.index = proc.lad.index;
				dataset.lad.breaks = getBreaks(proc.lad.data.map((d) => d.perc));

				dataset.ew.data = proc.ew.data;

				data[selectItem.code] = dataset;
				selectData = dataset;
				console.log("data loaded from nomis!");
				if (active.lad.selected) {
					setColors();
				}
				loading = false;
			});
		}
	}

	function doSelect() {
		if (active.lad.selected != active.lad.selectedPrev) {
			active.lad.selectedPrev = active.lad.selected;
			if (active.lad.selected && !ladlookup[active.lad.selected].children.includes(active.lsoa.selected)) {
				active.lsoa.selected = null;
			}
			setColors();
		}
	}

	function setColors() {
		let newdata = JSON.parse(JSON.stringify(data[selectItem.code]));
		if (active.lad.selected) {
			// re-color dataset
			newdata.lsoa.data.forEach((d) => {
				if (lsoalookup[d.code].parent == active.lad.selected) {
					d.fill = d.color;
					d.selected = true;
				} else {
					d.fill = d.muted;
					d.selected = false;
				}
			});
			// zoom to district on map
			let geometry = ladbounds.features.find(
				(f) => f.properties[ladtopo.code] == active.lad.selected
			).geometry;
			let bounds = bbox(geometry);
			if (!active.lsoa.selected) {
				map.fitBounds(bounds, { padding: 20 });
			}
		}
		selectData = newdata;
	}

	$: selectItem && setSelect(); // Update meta when selection updates
	$: active.lad.highlighted = active.lsoa.hovered ? lsoalookup[active.lsoa.hovered].parent : null;
	$: active.lad.selected = active.lsoa.selected ? lsoalookup[active.lsoa.selected].parent : active.lad.selected;
	$: (active.lad.selected || active.lad.selected == null) && doSelect();

	onMount(() => initialise());
</script>

<style>
	:global(body) {
		margin: 0;
		padding: 0;
	}
	h1 {
		margin-top: 0;
	}
	p {
		margin-bottom: 0;
	}
	ul {
		padding: 0.2em 0 0 1.2em;
		margin: 0;
	}
	li {
		padding: 0.2em 0 0 0;
	}
	hr {
		border: none;
		border-top: 3px solid grey;
	}
	#infobox {
		min-height: 200px;
	}
	.muted {
		color: #888;
	}
	.mt {
		margin-top: 5px;
	}
	.text-med {
		font-size: 1.5em;
		font-weight: bold;
	}
	.text-lrg {
		font-size: 2em;
		font-weight: bold;
	}
	.grid {
		display: grid;
		grid-gap: 12px;
		grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
		justify-items: stretch;
		width: 100%;
		margin: 0;
	}
</style>

{#if loading}
	<Loader
		height="100vh"
		width="100vw"
		position="fixed"
		bgcolor="rgba(255, 255, 255, 0.7)" />
{/if}

<Panel>
	<h1>Census Data Vis Atlas</h1>
	{#if indicators && selectItem}
		{#if selectData}
			<ColChart
				data={selectData.lsoa.data}
				dataIndex={selectData.lsoa.index}
				yMax={+selectData.lsoa.breaks[10]}
				yMin={+selectData.lsoa.breaks[0]}
				selected={active.lsoa.hovered ? active.lsoa.hovered : active.lsoa.selected}
				parent={active.lad.highlighted ? selectData.lad.index[active.lad.highlighted].median.code : active.lad.selected ? selectData.lad.index[active.lad.selected].median.code : null}
				siblings={active.lad.selected ? ladlookup[active.lad.selected].children : null}
				xKey="code"
				yKey="perc" />
		{/if}
		<div id="infobox">
			{selectMeta.table.name}
			<small>({selectMeta.table.code})</small><br />
			<strong class="text-med">{selectItem.name}</strong>
			<div class="grid">
				{#if selectData}
					<div>
						<hr style="border-top-color: #871A5B" />
						<strong>England & Wales</strong><br />
						<strong
							class="text-lrg">{selectData.ew.data.perc.toFixed(1)}%</strong><br />
						<small>{selectData.ew.data.value.toLocaleString()}
							of
							{selectData.ew.data.count.toLocaleString()}
							{selectItem.unit.toLowerCase()}s</small>
					</div>
					{#if active.lad.highlighted || active.lad.selected}
						<div>
							<hr style="border-top-color: #27A0CC" />
							<strong>{active.lad.highlighted ? ladlookup[active.lad.highlighted].name : ladlookup[active.lad.selected].name}</strong><br />
							<strong
								class="text-lrg">{active.lad.highlighted ? selectData.lad.index[active.lad.highlighted].perc.toFixed(1) : selectData.lad.index[active.lad.selected].perc.toFixed(1)}%</strong><br />
							<small>{active.lad.highlighted ? selectData.lad.index[active.lad.highlighted].value.toLocaleString() : selectData.lad.index[active.lad.selected].value.toLocaleString()}
								of
								{active.lad.highlighted ? selectData.lad.index[active.lad.highlighted].count.toLocaleString() : selectData.lad.index[active.lad.selected].count.toLocaleString()}
								{selectItem.unit.toLowerCase()}s</small>
						</div>
					{:else}
						<div />
					{/if}
					{#if active.lsoa.hovered || active.lsoa.selected}
						<div>
							<hr style="border-top-color: #000000" />
							<strong>{active.lsoa.hovered ? lsoalookup[active.lsoa.hovered].name.slice(-4) : lsoalookup[active.lsoa.selected].name.slice(-4)}</strong><br />
							<strong
								class="text-lrg">{active.lsoa.hovered ? selectData.lsoa.index[active.lsoa.hovered].perc.toFixed(1) : selectData.lsoa.index[active.lsoa.selected].perc.toFixed(1)}%</strong><br />
							<small>{active.lsoa.hovered ? selectData.lsoa.index[active.lsoa.hovered].value.toLocaleString() : selectData.lsoa.index[active.lsoa.selected].value.toLocaleString()}
								of
								{active.lsoa.hovered ? selectData.lsoa.index[active.lsoa.hovered].count.toLocaleString() : selectData.lsoa.index[active.lsoa.selected].count.toLocaleString()}
								{selectItem.unit.toLowerCase()}s</small>
						</div>
					{:else}
						<div />
					{/if}
				{/if}
			</div>
		</div>

		{#if ladlist}
			<Select
				options={ladlist}
				bind:selected={active.lad.selected}
				search={true}
				placeholder="Select a district"
				on:select={() => active.lsoa.selected = null} />
		{/if}

		<Group
			props={{ name: '2011 Census Tables', children: indicators.slice(0, 8) }}
			bind:selected={selectItem}
			expanded />
	{/if}
</Panel>

<Map bind:map style={mapstyle} minzoom={7} maxzoom={14} bind:zoom={mapZoom}>
	{#if selectData}
		<MapSource
			id="lsoa"
			type="vector"
			url={lsoabldg.url}
			layer={lsoabldg.layer}
			promoteId={lsoabldg.code}
			minzoom={7}>
			<MapLayer
				id="lsoa"
				source="lsoa"
				sourceLayer={lsoabldg.layer}
				data={selectData}
				type="fill"
				paint={{
					'fill-color': ['case',
						['!=', ['feature-state', 'color'], null], ['feature-state', 'color'],
						'rgba(255, 255, 255, 0)'
					]
				}}
				order="tunnel_motorway_casing" />
		</MapSource>
		<MapSource
			id="lsoa-bounds"
			type="vector"
			url={lsoabounds.url}
			layer={lsoabounds.layer}
			promoteId={lsoabounds.code}
			minzoom={9}>
			<MapLayer
				id="lsoa-fill"
				source="lsoa-bounds"
				sourceLayer={lsoabounds.layer}
				type="fill"
				paint={{ 'fill-color': 'rgba(255, 255, 255, 0)' }}
				hover={true}
				bind:hovered={active.lsoa.hovered}
				click={true}
				bind:selected={active.lsoa.selected} />
			<MapLayer
				id="lsoa-bounds"
				source="lsoa-bounds"
				sourceLayer={lsoabounds.layer}
				type="line"
				paint={{
					'line-color': ['case',
						['==', ['feature-state', 'selected'], true], 'rgba(0, 0, 0, 1)',
						['==', ['feature-state', 'hovered'], true], 'rgba(0, 0, 0, 1)',
						'rgba(0, 0, 0, 0)'
					],
					'line-width': ['case',
						['==', ['feature-state', 'selected'], true], 2,
						['==', ['feature-state', 'hovered'], true], 2,
						0
					]
				}} />
		</MapSource>
	{/if}
	{#if ladbounds}
		<MapSource
			id="lad"
			type="geojson"
			data={ladbounds}
			promoteId={ladtopo.code}>
			<MapLayer
				id="lad"
				source="lad"
				type="line"
				highlight={true}
				highlighted={active.lad.highlighted}
				paint={{
					'line-color': ['case',
						['==', ['feature-state', 'selected'], true], '#27A0CC',
						['==', ['feature-state', 'hovered'], true], '#27A0CC',
						['==', ['feature-state', 'highlighted'], true], '#27A0CC',
						'rgba(192, 192, 192, 1)'
					],
					'line-width': ['case',
						['==', ['feature-state', 'selected'], true], 2,
						['==', ['feature-state', 'hovered'], true], 2,
						['==', ['feature-state', 'highlighted'], true], 1.5,
						1
					]
				}}
				order="place_state" />
			<MapLayer
				id="lad-fill"
				source="lad"
				type="fill"
				paint={{ 'fill-color': 'rgba(255, 255, 255, 0)' }}
				hover={true}
				bind:hovered={active.lad.hovered}
				click={true}
				bind:selected={active.lad.selected}
				maxzoom={8.99}
				on:select={() => active.lsoa.selected = null} />
		</MapSource>
	{/if}
</Map>
